.article-micro {
	width: 100%;
	background-color: $colorHeaderBg;
	margin: 10px 0;
	border-top: 5px solid $colorPrimary;

	@media (--viewport-medium) {
		width: calc(50% - 10px);
		margin-right: 10px;

		&:nth-child(even) {
			margin-right: 0;
			margin-left: 10px;
		}
	}

	@media (--viewport-huge) {
		width: calc(100% / 3 - 18px);
		margin-right: 25px;
		margin-left: 0;

		&:nth-child(even) {
			margin-right: 25px;
			margin-left: 0;
		}

		&:nth-child(3n + 3) {
			margin-right: 0;
			margin-left: 0;
		}
	}

	&:hover {
		.micro-image:after {
			transition: background-color 0.2s ease-in;
			background-color: color($colorBlack a(5%));
		}
	}

	.micro-image {
		display: block;
		line-height: 0;
		position: relative;

		&:after {
			content: '';
			top: 0;
			left: 0;
			position: absolute;
			width: 100%;
			height: 100%;
			transition: background-color 0.2s ease-in;
		}
	}

	img {
		width: 100%;
	}

	header,
	footer {
		padding: 22px 26px 30px;
	}

	header {
		padding-bottom: 0;

		h2 {
			margin: 0;
			line-height: 1;

			a {
				font-size: 24px;
				font-family: $fontBlack;
				text-transform: uppercase;
				color: $colorGray;
			}
		}

		ul.tags {
			margin-bottom: 8px;

			li a {
				color: $colorPrimary;
				font-size: 12px;
				font-family: $fontBlack;
				text-transform: uppercase;
				padding: 0 5px 0 0;
			}
		}
	}

	footer {
		margin: 16px 0 0;
		padding-top: 0;
		display: flex;
		flex-direction: row;
		justify-content: space-between;

		.date ,
		.time {
			font-size: 12px;
			text-transform: uppercase;
			color: $colorGray;
		}
	}
}
