.btn-group {
	margin: 10px 0;
	display: flex;
}

.btn {
    line-height: 38px;
    font-size: 0.925rem;
	padding: 0 14px;
	margin: 0 4px;
    border: none;
	font-family: $fontReg;
	color: $colorWhite;
    background-color: $colorPrimary;
    transition: background-color 0.1s ease-in;
	border-radius: 3px;
	font-weight: bold;

	&:focus {
		outline: none;
	}

	&:first-child {
		margin-left: 0;
	}

    &:hover {
        background-color: color($colorPrimary lightness(50%));
		transition: background-color 0.1s ease-in;
    }

    &.btn--primary {
		background-color: $colorPrimary;
		color: $colorWhite;

	    &:hover {
	        background-color: color($colorPrimary a(80%));
	    }
    }

    &.btn--secondary {
		background-color: $colorSecondary;
		color: $colorWhite;

		&:hover {
	        background-color: color($colorSecondary a(80%));
	    }
    }

	&.btn--alternative {
		background-color: $colorGrayLight;
		color: $colorWhite;

		&:hover {
			background-color: color($colorSecondary a(80%));
		}
	}

	&.btn--outline {
		background-color: transparent;
		border: 2px solid $colorWhite;

		&:hover {
			background-color: $colorWhite;
		    transition: background-color 0.1s ease-in, color 0.1s ease-in;
			color: $colorGray;
		}
	}

	&.btn--icon:before {
		padding-right: 5px;
		vertical-align: middle;
		line-height: 38px;
	}
}
