@font-face {
	font-family: 'icons';
	src:url('../fonts/icons.eot?npyyni');
	src:url('../fonts/icons.eot?npyyni#iefix') format('embedded-opentype'),
		url('../fonts/icons.woff2?npyyni') format('woff2'),
		url('../fonts/icons.woff?npyyni') format('woff'),
		url('../fonts/icons.ttf?npyyni') format('truetype');
	font-weight: normal;
	font-style: normal;
}

[class^="icon-"]:before, [class*=" icon-"]:before {
	font-family: 'icons';
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

